@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
body{font-family:"Raleway", Arial, sans-serif;margin:0}.app{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:50vh}.app__title{text-align:center}.app__form{display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:20px;gap:20px}.app__input{display:flex;justify-content:space-between;grid-gap:10px;gap:10px;width:100%}

.Carousel{display:flex;flex-direction:row;align-items:center;justify-content:center;grid-gap:30px;gap:30px}.Carousel__wrapper{overflow:hidden}.Carousel__list{display:flex;list-style:none;padding:0}.Carousel__button{border:none;background:transparent;cursor:pointer;font-size:30px}

